body {
  margin: 0;
}

.stopwatch {
  border: 1vmin dotted #000;
  border-radius: 50%;
  width: 80vmin;
  height: 80vmin;
  position: relative;
}

.stopwatch__minutes {
  animation: rotate;
  animation-duration: 60min;
  transform-origin: bottom;
  background-color: #0700ff;
  width: 3vmin;
  height: 20vmin;
  margin: auto;
  animation-timing-function: steps(60, end);
  animation-iteration-count: infinite;
  position: absolute;
  inset: 0 0 25%;
}

.stopwatch__minutes--speed-up {
  animation-duration: 10min;
}

.stopwatch__seconds {
  transform-origin: bottom;
  background-color: #2c8000;
  width: 1.5vmin;
  height: 38vmin;
  margin: auto;
  animation: 60s linear infinite rotate;
  position: absolute;
  inset: 0 0 50%;
}

.stopwatch__seconds--speed-up {
  animation-duration: 10s;
}

.stopwatch__center {
  background-color: #f6a603;
  border-radius: 50%;
  width: 5vmin;
  height: 5vmin;
  margin: auto;
  position: absolute;
  inset: 0;
}

@keyframes rotate {
  0% {
    transform: rotateZ(0);
  }

  25% {
    transform: rotateZ(90deg);
  }

  50% {
    transform: rotateZ(180deg);
  }

  75% {
    transform: rotateZ(240deg);
  }

  100% {
    transform: rotateZ(360deg);
  }
}
